@import url(https://fonts.googleapis.com/css?family=Barlow+Condensed:500|Lato:400,700&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

* {
  font-family: "Lato", sans-serif; }

.game-title,
.review-title {
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 500; }

p,
a,
button,
label,
input {
  font-weight: 400; }

.section-title {
  color: #FFFFFF;
  margin-bottom: 0.5rem;
  font-weight: 400;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  padding-bottom: 0.5rem;
  font-size: 1rem; }

html {
  font-size: 16px; }

body {
  min-height: 100vh;
  padding: 3rem 8px 4rem; }

a {
  text-decoration: none; }

img, video {
  width: 100%;
  display: block; }

input {
  border: none;
  border-bottom: 1px solid;
  padding: 0.5rem 0; }

input:focus {
  outline: none; }

.button {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #FFFFFF;
  padding: .25rem 1rem;
  text-align: center;
  color: #FFFFFF;
  font-size: 13px;
  line-height: 16px;
  display: inline-block; }

.user-avatar {
  width: 32px;
  height: 32px;
  background: #00ff68;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border-radius: 50%; */
  font-family: Lato-Black;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.8);
  line-height: 17px; }

.navbar {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 100; }

.main-menu {
  display: flex;
  justify-content: center;
  padding: 0.5rem 0; }

.main-menu-item:not(:last-child) {
  margin-right: 2.5rem; }

.main-menu-link {
  font-size: 7px;
  line-height: 8px;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  align-items: center; }

.main-menu-link svg {
  width: 1rem;
  height: 1rem;
  margin-bottom: 0.5rem; }

.home-form {
  padding: 3rem;
  min-width: 224px; }

.home-form .field:not(:last-child) {
  margin-bottom: 1.5rem; }

.home-form input {
  width: 100%; }

.home-form input[type="submit"] {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-image: linear-gradient(269deg, rgba(29,30,48,0.99) 2%, #17141D 98%);
  background-image: var(--secondary-background-color);
  font-size: 15px;
  font-weight: 700;
  left: 0;
  color: #FFFFFF;
  color: var(--primary-text-color);
  border-bottom: none;
  background-image: linear-gradient(269deg, rgba(29, 30, 48, 0.99) 2%, #17141D 98%);
  padding: 1.5rem 0; }

.account-question {
  text-align: center;
  padding: 1.5rem 0;
  background-color: rgba(0, 0, 0, .10);
  background-color: var(--primary-background-color); }
  .account-question p {
    font-size: 13px;
    line-height: 16px;
    color: #FFFFFF;
    color: var(--primary-text-color); }

.social-login {
  text-align: center;
  padding: 1.5rem 0; }

.field-genre-checkbox input,
.field-platform-checkbox input {
  display: none; }
  .field-genre-checkbox input:checked + label,
  .field-platform-checkbox input:checked + label {
    color: #000000;
    color: var(--on-primary);
    background-color: #00FF68;
    background-color: var(--primary); }

.field-genre-checkbox label,
.field-platform-checkbox label {
  padding: .25rem .5rem;
  border: 1px solid #00FF68;
  border: 1px solid var(--primary);
  font-size: 9px;
  color: #00FF68;
  color: var(--primary);
  display: inline-block;
  text-transform: uppercase;
  margin: 4px; }

.form-section {
  margin-bottom: 1rem; }

.form-section-content-tags {
  display: flex;
  flex-wrap: wrap; }

.games-form {
  margin-bottom: 1rem; }

.games-list li,
.users-list li {
  padding: 0.5rem;
  background-color: rgba(0, 0, 0, .10);
  background-color: var(--primary-background-color);
  margin-bottom: 0.5rem; }
  .games-list li:last-child,
  .users-list li:last-child {
    margin-bottom: 1rem; }

.games-list a,
.users-list a {
  color: #FFFFFF;
  font-size: 13px; }

.search {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.1); }
  .search input[type="search"] {
    border-bottom: none;
    padding-left: 0.5rem;
    width: 85%; }
  .search .close-search-results {
    background-color: #00FF68;
    background-color: var(--primary);
    color: #000;
    position: fixed;
    top: 0.4rem;
    right: 0.5rem;
    display: block;
    padding: 0.25rem 1rem;
    font-size: 9px;
    text-transform: uppercase; }

.search-results {
  position: fixed;
  top: 29px;
  background-color: rgba(29, 29, 31, 0.7);
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-backdrop-filter: saturate(180%) blur(3px);
          backdrop-filter: saturate(180%) blur(3px);
  width: 100%;
  height: calc(100vh - 29px);
  padding: 1rem;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px; }
  .search-results h2 {
    color: #00FF68;
    color: var(--primary); }
  .search-results .results {
    margin-bottom: 1rem;
    color: #FFFFFFAA; }
    .search-results .results h3 {
      color: #FFFFFF;
      margin-bottom: 0.5rem;
      font-weight: 400;
      border-bottom: 1px solid rgba(255, 255, 255, 0.25);
      padding-bottom: 0.5rem; }
    .search-results .results a {
      font-size: 13px;
      color: #FFFFFFaa; }
      .search-results .results a:not(:last-child) {
        margin-bottom: 0.5rem;
        line-height: 16px; }

.chat-users li {
  border: 1px solid #00FF68;
  border: 1px solid var(--primary);
  margin-right: .5rem;
  color: #FFFFFF;
  font-size: 9px;
  text-transform: uppercase;
  padding: 0.25rem 0.5rem; }

.chat-users-list {
  display: flex; }

.other-profile *,
.user-reviews {
  color: #FFFFFFaa; }

.home-game-section {
  color: #FFFFFFAA; }

.review-tile {
  position: relative;
  border: 1px solid transparent; }

.review-tile-game-cover {
  border: 1px solid transparent;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
  opacity: 0.25;
  height: 170px;
  background-color: rgba(0, 0, 0, 0.4); }

.review-tile:hover {
  border-color: #00FF68;
  border-color: var(--primary); }

.landing {
  color: #FFFFFFAA; }

.scrollable-section {
  overflow: auto; }
  .scrollable-section img, .scrollable-section video, .scrollable-section iframe {
    width: 300px; }

.scrollable-section ul {
  display: flex; }

.game-section-content {
  line-height: 20px;
  font-size: 13px; }

.game-section {
  color: #FFFFFFaa;
  padding: 1rem 0; }

.review-tile-content {
  display: flex;
  color: #FFFFFF;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 10;
  padding: 1rem; }

.review-tile-rating {
  margin-right: 1rem; }

.review-title-resume {
  z-index: 100; }
  .review-title-resume h2 {
    margin-bottom: 0.5rem; }
  .review-title-resume p {
    color: #FFFFFFaa; }

:root {
  --primary: #00FF68;
  --on-primary: #000000;
  --primary-text-color: #FFFFFF;
  --primary-background-color: rgba(0, 0, 0, .10);
  --secondary-background-color: linear-gradient(269deg, rgba(29,30,48,0.99) 2%, #17141D 98%);
  --secondary-text-color: rgba(0, 0, 0, .60);
  --body-background: linear-gradient(180deg, rgba(29,30,48,0.99) 4%, #17141D 100%);
  --input-border-color: #FFFFFF;
  --navbar-background: #000000;
  --main-menu-link-color: rgba(255, 255, 255, .5);
  --placeholder-color: rgba(255, 255, 255, .5); }

body {
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(4%, rgba(29,30,48,0.99)), to(#17141D));
  background-image: linear-gradient(180deg, rgba(29,30,48,0.99) 4%, #17141D 100%);
  background-image: var(--body-background); }

input {
  background-color: transparent;
  color: #FFFFFF;
  border-bottom-color: #FFFFFF;
  border-bottom-color: var(--input-border-color); }

input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, .5);
  color: var(--placeholder-color); }

input::-moz-placeholder {
  color: rgba(255, 255, 255, .5);
  color: var(--placeholder-color); }

input:-ms-input-placeholder {
  color: rgba(255, 255, 255, .5);
  color: var(--placeholder-color); }

input::-ms-input-placeholder {
  color: rgba(255, 255, 255, .5);
  color: var(--placeholder-color); }

input::placeholder {
  color: rgba(255, 255, 255, .5);
  color: var(--placeholder-color); }

input:focus {
  color: #00FF68;
  color: var(--primary);
  border-bottom-color: #00FF68;
  border-bottom-color: var(--primary); }

a {
  color: #00FF68;
  color: var(--primary); }

.navbar {
  background-color: #000000;
  background-color: var(--navbar-background); }

.main-menu-link {
  color: rgba(255, 255, 255, .5);
  color: var(--main-menu-link-color); }

.main-menu-link svg {
  stroke: rgba(255, 255, 255, .5);
  stroke: var(--main-menu-link-color); }

